<template>
<!-- 請同學自行新增 v-model -->
<div class="modal" ref="modal">
  <div class="modal-dialog modal-xl" role="document">
  <div class="modal-content border-0">
    <div class="modal-header bg-dark text-white">
      <h5 class="modal-title" id="exampleModalLabel">
        <span>新增產品</span>
      </h5>
      <button type="button" class="btn-close"
              data-bs-dismiss="modal" aria-label="Close"></button>
    </div>
    <div class="modal-body">
      <div class="row">
        <div class="col-sm-4">
          <div class="mb-3">
            <label for="image" class="form-label">輸入圖片網址</label>
            <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl"
                    placeholder="請輸入圖片連結">
          </div>
          <div class="mb-3">
            <label for="customFile" class="form-label">或 上傳圖片
              <i class="fas fa-spinner fa-spin"></i>
            </label>
            <input type="file" id="customFile" class="form-control" @change="uploadFile" ref="fileInput">
          </div>
          <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
          <!-- 延伸技巧，多圖 -->
          <div class="mt-5" v-if="tempProduct.images">
            <div v-for="(image, key) in tempProduct.images" class="mb-3 input-group" :key="key">
              <input type="url" class="form-control form-control"  v-model="tempProduct.images[key]"
                      placeholder="請輸入連結">
              <button type="button" class="btn btn-outline-danger" @click="tempProduct.images.splice(key, 1)">
                移除
              </button>
            </div>
            <div v-if="
                    tempProduct.images[tempProduct.images.length - 1] || !tempProduct.images.length
                  ">
              <button class="btn btn-outline-primary btn-sm d-block w-100" @click="tempProduct.images.push('')">
                新增圖片
              </button>
            </div>
          </div>
        </div>
        <div class="col-sm-8">
          <div class="mb-3">
            <label for="title" class="form-label">標題</label>
            <input type="text" class="form-control" id="title" v-model="tempProduct.title"
                    placeholder="請輸入標題">
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="category" class="form-label">分類</label>
              <input type="text" class="form-control" id="category" v-model="tempProduct.category"
                      placeholder="請輸入分類">
            </div>
            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">單位</label>
              <input type="text" class="form-control" id="unit" v-model="tempProduct.unit"
                      placeholder="請輸入單位">
            </div>
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="origin_price" class="form-label">原價</label>
              <input type="number" class="form-control" id="origin_price"  v-model.number="tempProduct.origin_price"
                      placeholder="請輸入原價">
            </div>
            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">售價</label>
              <input type="number" class="form-control" id="price"  v-model.number="tempProduct.price"
                      placeholder="請輸入售價">
            </div>
          </div>
          <hr>

          <div class="mb-3">
            <label for="description" class="form-label">產品描述</label>
            <textarea type="text" class="form-control" id="description"  v-model="tempProduct.description"
                      placeholder="請輸入產品描述"></textarea>
          </div>
          <div class="mb-3">
            <label for="content" class="form-label">說明內容</label>
            <textarea type="text" class="form-control" id="content"  v-model="tempProduct.content"
                      placeholder="請輸入產品說明內容"></textarea>
          </div>
          <div class="mb-3">
            <div class="form-check">
              <input class="form-check-input" type="checkbox"   v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled">
              <label class="form-check-label" for="is_enabled">
                是否啟用
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-secondary"
              data-bs-dismiss="modal">取消
      </button>
      <button type="button" class="btn btn-primary" @click="$emit('update-product', tempProduct)">確認</button>
    </div>
  </div>
</div></div>
</template>

<script>
// 調用 BS 方法，必須先 modal 相關的方法 import 進來
import modalMixin from '@/mixins/modalMixin'
export default {
  props: {
    // 外層 tempProduct 傳進來的資料透過 props product 接收
    product: {
      // 預期傳進來是物件型別
      type: Object,
      // 如果沒有正確傳入，則給予預設值
      default () {
        return {}
      }
    }
  },
  watch: {
    // 監聽 prop product 的資料變動，有變動就會將使用者寫入的資料寫入下面定義的 tempProduct
    product () {
      this.tempProduct = this.product
    }
  },
  // 將建立的實體內容回傳出來
  data () {
    return {
      modal: {},
      // 作為外層資料的接收
      tempProduct: {}
    }
  },
  // 新增方法讓外部元件可以呼叫它
  methods: {
    uploadFile () {
      // 取得 input dom 元素的 file 屬性，可用 console.dir(this.$refs.fileInput)查看
      console.dir(this.$refs.fileInput)
      const uploadedFile = this.$refs.fileInput.files[0]
      // 轉成 formdata 的格式
      const formData = new FormData()
      // 增加一個欄位到表單裡；表單有一個name屬性的值為 file-to-upload，所帶的內容為我們取到的檔案
      formData.append('file-to-upload', uploadedFile)
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.$http.post(url, formData).then((res) => {
        // res.data 會獲得圖片上傳至伺服器儲存的路徑 imageUrl
        if (res.data.success) {
          this.tempProduct.imageUrl = res.data.imageUrl
        }
      }
      )
    }
  },
  mixins: [modalMixin]
}
</script>
